import React, { Component } from 'react'

export default class App extends Component {

  state = {
    name: "jashos",
    isEdit: false
  }

  editInput = () => {
    this.setState({ isEdit: true });
  }

  render() {
    return (
      <div>
        {!isEdit ?
          <h1>{this.state.name}</h1> :
          <input>{this.state.name}</input>
        }
        <button onClick={editInput()}>Edit</button>
      </div>
    )
  }
}
